 flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  background-color: #f7ab4d;\r\n  min-height: 100px;\r\n  color: white;\r\n  padding: 1rem;\r\n  text-align: center;\r\n  border-radius: 5px;\r\n}\r\n\r\n.header h1 {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n/* CONTAINER STYLES */\r\n\r\n#main-container {\r\n  display: flex;\r\n  justify-content: space-around;\r\n  flex-wrap: wrap;\r\n  padding: 2rem;\r\n  width: 100%;\r\n  margin: 0;\r\n}\r\n\r\n/* CARD STYLES */\r\n\r\n.card {\r\n  width: 45%;\r\n  min-width: 300px;\r\n\r\n  margin-bottom: 20px;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\r\n  background: linear-gradient(to bottom right, #fcb0a3, #d86779);\r\n}\r\n\r\n.card-header {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  min-height: 75px;\r\n  border-bottom: none;\r\n  background-color: rgba(0, 0, 0, 0);\r\n}\r\n\r\n.card-body {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  min-height: 300px;\r\n  padding: 0px;\r\n  margin: 0px;\r\n}\r\n\r\n.card-footer {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  border-top: none;\r\n  padding: 10px;\r\n  min-height: 75px;\r\n  background-color: rgba(0, 0, 0, 0);\r\n}\r\n\r\n.footer-text {\r\n  font-size: 12px;\r\n  font-weight: bold;\r\n  color: #fff;\r\n}\r\n\r\n@media screen and (max-width: 991px) {\r\n  .footer-text {\r\n    font-size: 10px;\r\n  }\r\n}\r\n\r\n/* DESTINATION FORM STYLES */\r\n\r\n.destination-card-body {\r\n  margin: auto;\r\n}\r\n\r\n.destination-form {\r\n  max-width: 400px;\r\n  margin: auto;\r\n}\r\n\r\n.destination {\r\n  width: 100%;\r\n  padding: 10px;\r\n  border: 1px solid #ced4da;\r\n  border-radius: 4px;\r\n}\r\n\r\n.travel-date {\r\n  display: block;\r\n  margin-bottom: 5px;\r\n  font-weight: bold;\r\n}\r\n\r\ninput {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.input[type=\"date\"] {\r\n  width: 100%;\r\n  padding: 10px